import { format, groupFy, KVMapItem, VALIDATOR } from '@/utils/func'
import { defineComponent, reactive } from 'vue'
import {
  binaryToDecimal,
  binaryToHex,
  binaryToOctal,
  decimalToBinary,
  decimalToHex,
  decimalToOctal,
  hexToBinary,
  hexToDecimal,
  hexToOctal,
  octalToBinary,
  octalToDecimal,
  octalToHex
} from '@/utils/convertor'

const OPTIONS: KVMapItem<string, string>[] = [
  { key: '二进制', value: 'binary' },
  { key: '八进制', value: 'octal' },
  { key: '十进制', value: 'decimal' },
  { key: '十六进制', value: 'hex' }
]

interface SourceTargetMapItem {
  source: string
  target: string
  validator: RegExp
  convert: (s: string) => string
}

/**
 * Map source radix and target radix to convertor function
 */
const SOURCE_TARGET_MAP: SourceTargetMapItem[] = [
  // From binary
  { source: 'binary', target: 'binary', validator: VALIDATOR.binary, convert: s => groupFy(s, 4).target },
  { source: 'binary', target: 'octal', validator: VALIDATOR.binary, convert: s => binaryToOctal(s) },
  { source: 'binary', target: 'decimal', validator: VALIDATOR.binary, convert: s => binaryToDecimal(s) },
  { source: 'binary', target: 'hex', validator: VALIDATOR.binary, convert: s => binaryToHex(s) },
  // From octal
  { source: 'octal', target: 'binary', validator: VALIDATOR.octal, convert: s => octalToBinary(s) },
  { source: 'octal', target: 'octal', validator: VALIDATOR.octal, convert: s => format(s).target },
  { source: 'octal', target: 'decimal', validator: VALIDATOR.octal, convert: s => octalToDecimal(s) },
  { source: 'octal', target: 'hex', validator: VALIDATOR.octal, convert: s => octalToHex(s) },
  // From decimal
  { source: 'decimal', target: 'binary', validator: VALIDATOR.decimal, convert: s => decimalToBinary(s) },
  { source: 'decimal', target: 'octal', validator: VALIDATOR.decimal, convert: s => decimalToOctal(s) },
  { source: 'decimal', target: 'decimal', validator: VALIDATOR.decimal, convert: s => format(s).target },
  { source: 'decimal', target: 'hex', validator: VALIDATOR.decimal, convert: s => decimalToHex(s) },
  // From hex
  { source: 'hex', target: 'binary', validator: VALIDATOR.hex, convert: (s: string): string => hexToBinary(s) },
  { source: 'hex', target: 'octal', validator: VALIDATOR.hex, convert: (s: string): string => hexToOctal(s) },
  { source: 'hex', target: 'decimal', validator: VALIDATOR.hex, convert: (s: string): string => hexToDecimal(s) },
  { source: 'hex', target: 'hex', validator: VALIDATOR.hex, convert: (s: string): string => format(s).target }
]

export default defineComponent({
  setup() {
    const source = reactive({ radix: 'decimal', input: '' })
    const target = reactive({ radix: 'binary', input: '' })
    const onAnyChange = () => {
      source.input = source.input.toLowerCase()
      if (source.input === '') {
        target.input = ''
      } else {
        const { validator, convert: convert } = SOURCE_TARGET_MAP.find(
          item => item.source === source.radix && item.target === target.radix
        ) as SourceTargetMapItem
        target.input = validator.test(source.input) ? convert(source.input) : '无效'
      }
    }
    return () => {
      const radios = OPTIONS.map(item => <a-radio-button value={item.value}>{item.key}</a-radio-button>)
      return (
        <a-card style='width: 500px' hoverable={true}>
          <a-row gutter={[8, 8]}>
            <a-col>
              <a-radio-group v-model={[source.radix, 'value']} onChange={onAnyChange}>
                {radios}
              </a-radio-group>
            </a-col>
          </a-row>
          <a-row gutter={[8, 8]}>
            <a-col>
              <a-radio-group v-model={[target.radix, 'value']} onChange={onAnyChange}>
                {radios}
              </a-radio-group>
            </a-col>
          </a-row>
          <a-row gutter={[8, 8]}>
            <a-col>
              <a-input v-model={[source.input, 'value']} placeholder='源进制' onChange={onAnyChange} />
            </a-col>
          </a-row>
          <a-row gutter={[8, 8]}>
            <a-col>
              <a-input v-model={[target.input, 'value']} placeholder='目标进制' disabled={true} />
            </a-col>
          </a-row>
        </a-card>
      )
    }
  }
})
