/**
 * K-V map item
 */
interface KVMapItem<K, V> {
  key: K,
  value: V
}

/**
 * Map<Binary, Octal>
 */
const BINARY_OCTAL_MAP: KVMapItem<string, string>[] = [
  { key: '000', value: '0' },
  { key: '001', value: '1' },
  { key: '010', value: '2' },
  { key: '011', value: '3' },
  { key: '100', value: '4' },
  { key: '101', value: '5' },
  { key: '110', value: '6' },
  { key: '111', value: '7' }
]
/**
 * Map<Binary, Hex>
 */
const BINARY_HEX_MAP: KVMapItem<string, string>[] = [
  { key: '0000', value: '0' },
  { key: '0001', value: '1' },
  { key: '0010', value: '2' },
  { key: '0011', value: '3' },
  { key: '0100', value: '4' },
  { key: '0101', value: '5' },
  { key: '0110', value: '6' },
  { key: '0111', value: '7' },
  { key: '1000', value: '8' },
  { key: '1001', value: '9' },
  { key: '1010', value: 'A' },
  { key: '1011', value: 'B' },
  { key: '1100', value: 'C' },
  { key: '1101', value: 'D' },
  { key: '1110', value: 'E' },
  { key: '1111', value: 'F' }
]

/**
 * Regex to validate radix sequence
 */
const VALIDATOR = {
  binary: /^[01]+(\.[01]+)?$/,
  octal: /^[0-7]+(\.[0-7]+)?$/,
  decimal: /^[0-9]+(\.[0-9]+)?$/,
  hex: /^[0-9a-fA-F]+(\.[0-9a-fA-F]+)?$/
}

/**
 * Return type used by `format()` and `groupBy()`
 */
interface Formatted {
  // Formatted sequence
  target: string,
  // Formatted sequence before dot
  before: string,
  // Formatted sequence after dot
  after: string,
  // Integral part of number
  int: string,
  // Fracational part of number
  frac: string
}

/**
 * Change to uppercase and remove redundant zero
 * @param sequence
 */
function format(sequence: string): Formatted {
  if (!VALIDATOR.hex.test(sequence)) {
    throw new Error(`${sequence} is invalid`)
  }
  // Change to upppercase
  sequence = sequence.toUpperCase()
  const dot = sequence.indexOf('.')
  let [before, after] = (dot === -1) ? [sequence, ''] : [sequence.slice(0, dot), sequence.slice(dot + 1)]
  // Remove redundant zero before the dot
  before = before.replace(/^(0+)/, '')
  if (before === '') {
    before = '0'
  }
  // Remove redundant zero after the dot
  after = after.replace(/0+$/, '')
  return {
    target: (after === '') ? before : (before + '.' + after),
    before,
    after,
    int: before,
    frac: (after === '') ? '0' : ('0.' + after)
  }
}

/**
 * Add zero to make sequence several groups based on specified value
 * @param sequence
 * @param group
 */
function groupFy(sequence: string, group: number): Formatted {
  let { before, after, int, frac } = format(sequence)
  const [ri, rf] = [before.length % group, after.length % group]
  // Add zero before dot
  if (ri !== 0) {
    for (let i = 0; i < group - ri; i++) {
      before = '0' + before
    }
  }
  // Add zero after dot
  if (rf !== 0) {
    for (let i = 0; i < group - rf; i++) {
      after = after + '0'
    }
  }
  return {
    target: (after === '') ? before : (before + '.') + after,
    before,
    after,
    int,
    frac
  }
}


/**
 * Map and concatenate binary sequence which is serveral groups based on specified value
 * @param binary
 * @param map
 * @param group
 * @param orElse
 */
function mapByGroup(
  binary: string,
  map: KVMapItem<string,
    string>[],
  group: number,
  orElse: () => string = () => '?'
): string {
  if (binary.length % group !== 0) {
    throw new Error(`${binary} must be ${group}-based groups`)
  }
  let result = ''
  for (let i = 0; i < binary.length; i += group) {
    const item = map.find(item => item.key === binary.slice(i, i + group))
    result += (item === undefined) ? orElse() : item.value
  }
  return result
}


/**
 * Map a specified char to a number based on hex
 * @param char
 * @param orElse
 */
function charToDecimalNumber(char: string, orElse: () => number = () => NaN): number {
  if (char.length !== 1) {
    throw new Error(`${char} must be a char`)
  }
  const ascii = char.toUpperCase().charCodeAt(0)
  if (ascii >= 48 && ascii <= 57) {
    // 0 ~9
    return ascii - 48
  } else if (ascii >= 65 && ascii <= 70) {
    // A-F
    return ascii - 65 + 10
  } else {
    return orElse()
  }
}

/**
 * Map a specified number to a char based on hex
 * @param decimal
 * @param orElse
 */
function decimalNumberToChar(decimal: number, orElse: () => string = () => '?'): string {
  if (decimal >= 0 && decimal <= 9) {
    // 0-9
    return decimal + ''
  } else if (decimal >= 10 && decimal <= 15) {
    // A-F
    return String.fromCharCode(decimal + 65 - 10)
  } else {
    return orElse()
  }
}

/**
 * Any radix ==> Decimal. Max support to hex
 * @param sequence
 * @param radix
 */
function anyToDecimal(sequence: string, radix: number): string {
  if (!VALIDATOR.hex.test(sequence) || !(radix >= 2 && radix <= 16)) {
    throw new Error(`supppor max to hex`)
  }
  sequence = sequence.toUpperCase()
  const { before, after } = format(sequence)
  let target = 0
  for (let i = 0; i < before.length; i++) {
    const decimal = charToDecimalNumber(before[i])
    target += decimal * radix ** (before.length - 1 - i)
  }
  if (after !== '') {
    for (let i = 0; i < after.length; i++) {
      const decimal = charToDecimalNumber(after[i])
      target += decimal * radix ** (-(i + 1))
    }
  }
  return target + ''
}

/**
 * Decimal ==> Any radix. Max support to hex
 * @param decimal
 * @param radix
 * @param limit
 */
function decimalToAny(decimal: string, radix: number, limit: number = 2 ** 4): string {
  if (!VALIDATOR.decimal.test(decimal)) {
    throw new Error(`${decimal} is an invalid decimal sequence`)
  }
  const formatted = format(decimal)
  let [int, frac] = [parseInt(formatted.int), parseFloat(formatted.frac)]
  let [before, after] = ['', '']
  // Divided by k and record the remainder
  while (int !== 0) {
    const r = int % radix
    before = decimalNumberToChar(r) + before
    int = Math.floor(int / radix)
  }
  if (before === '') {
    before = '0'
  }
  let count = 0
  // Multiplied by k and record the integer
  while (frac !== 0 && (count++) < limit) {
    frac = frac * radix
    const i = Math.floor(frac)
    after = after + decimalNumberToChar(i)
    if (i >= 1) {
      frac = frac - i
    }
  }
  return (after === '') ? before : (before + '.' + after)
}


export {
  BINARY_OCTAL_MAP,
  BINARY_HEX_MAP,
  VALIDATOR,
  format,
  groupFy,
  mapByGroup,
  charToDecimalNumber,
  decimalNumberToChar,
  anyToDecimal,
  decimalToAny,
  KVMapItem
}
