import { anyToDecimal, BINARY_HEX_MAP, BINARY_OCTAL_MAP, decimalToAny, format, groupFy, KVMapItem, mapByGroup, VALIDATOR } from '@/utils/func'

// ======================================= 二进制 =======================================
/**
 * Binary ==> Octal
 * @param binary
 */
function binaryToOctal(binary: string): string {
  if (!VALIDATOR.binary.test(binary)) {
    throw new Error(`${binary} is an invalid binary sequence`)
  }
  const group = 3
  const { before, after } = groupFy(binary, group)
  let target = (before === '') ? '0' : mapByGroup(before, BINARY_OCTAL_MAP, group)
  if (after !== '') {
    target += '.' + mapByGroup(after, BINARY_OCTAL_MAP, group)
  }
  return format(target).target
}

/**
 * Binary ==> Decimal
 * @param binary
 */
function binaryToDecimal(binary: string): string {
  if (!VALIDATOR.binary.test(binary)) {
    throw new Error(`${binary} is an invalid binary sequence`)
  }
  return anyToDecimal(binary, 2)
}

/**
 * Binary ==> Hex
 * @param binary
 */
function binaryToHex(binary: string): string {
  if (!VALIDATOR.binary.test(binary)) {
    throw new Error(`${binary} is an invalid binary sequence`)
  }
  const group = 4
  const { before, after } = groupFy(binary, group)
  let target = (before === '') ? '0' : mapByGroup(before, BINARY_HEX_MAP, group)
  if (after !== '') {
    target += '.' + mapByGroup(after, BINARY_HEX_MAP, group)
  }
  return format(target).target
}


// ======================================= 八进制 =======================================
/**
 * Octal ==> Binary
 * @param octal
 */
function octalToBinary(octal: string): string {
  if (!VALIDATOR.octal.test(octal)) {
    throw new Error(`${octal} is an invalid octal sequence`)
  }
  const { before, after } = format(octal)
  let target = ''
  for (let char of before) {
    target += (BINARY_OCTAL_MAP.find(item => item.value === char) as KVMapItem<string, string>).key
  }
  if (after !== '') {
    target += '.'
    for (let char of after) {
      target += (BINARY_OCTAL_MAP.find(item => item.value === char) as KVMapItem<string, string>).key
    }
  }
  return groupFy(target, 3).target
}

/**
 * Octal ==> Decimal
 * @param octal
 */
function octalToDecimal(octal: string): string {
  if (!VALIDATOR.octal.test(octal)) {
    throw new Error(`${octal} is an invalid octal sequence`)
  }
  return anyToDecimal(octal, 8)
}

/**
 * Octal ==> Hex
 * @param octal
 */
function octalToHex(octal: string): string {
  if (!VALIDATOR.octal.test(octal)) {
    throw new Error(`${octal} is an invalid octal sequence`)
  }
  const binary = octalToBinary(octal)
  return binaryToHex(binary)
}


// ======================================= 十进制 =======================================
/**
 * Decimal ==> Binary
 * @param decimal
 */
function decimalToBinary(decimal: string): string {
  if (!VALIDATOR.decimal.test(decimal)) {
    throw new Error(`${decimal} is an invalid decimal sequence`)
  }
  const binary = decimalToAny(decimal, 2)
  return groupFy(binary, 4).target
}

/**
 * Decimal ==> Octal
 * @param decimal
 */
function decimalToOctal(decimal: string): string {
  if (!VALIDATOR.decimal.test(decimal)) {
    throw new Error(`${decimal} is an invalid decimal sequence`)
  }
  return decimalToAny(decimal, 8)
}

/**
 * Decimal ==> Hex
 * @param decimal
 */
function decimalToHex(decimal: string): string {
  if (!VALIDATOR.decimal.test(decimal)) {
    throw new Error(`${decimal} is an invalid decimal sequence`)
  }
  return decimalToAny(decimal, 16)
}


// ======================================= 十六进制 =======================================
/**
 * Hex ==> Binary
 * @param hex
 */
function hexToBinary(hex: string): string {
  if (!VALIDATOR.hex.test(hex)) {
    throw new Error(`${hex} is an invalid hex sequence`)
  }
  const { before, after } = format(hex)
  let target = ''
  for (let char of before) {
    target += (BINARY_HEX_MAP.find(item => item.value === char) as KVMapItem<string, string>).key
  }
  if (after !== '') {
    target += '.'
    for (let char of after) {
      target += (BINARY_HEX_MAP.find(item => item.value === char) as KVMapItem<string, string>).key
    }
  }
  return groupFy(target, 4).target
}

/**
 * Hex ==> Binary
 * @param hex
 */
function hexToOctal(hex: string): string {
  if (!VALIDATOR.hex.test(hex)) {
    throw new Error(`${hex} is an invalid hex sequence`)
  }
  const binary = hexToBinary(hex)
  return binaryToOctal(binary)
}

/**
 * Hex ==> Decimal
 * @param hex
 */
function hexToDecimal(hex: string): string {
  if (!VALIDATOR.hex.test(hex)) {
    throw new Error(`${hex} is an invalid hex sequence`)
  }
  return anyToDecimal(hex, 16)
}

export {
  binaryToOctal,
  binaryToDecimal,
  binaryToHex,
  octalToBinary,
  octalToDecimal,
  octalToHex,
  decimalToBinary,
  decimalToOctal,
  decimalToHex,
  hexToBinary,
  hexToOctal,
  hexToDecimal
}