import { defineAsyncComponent, defineComponent } from 'vue'
import { Input } from 'ant-design-vue'


const ComplexInput = defineComponent({
  props: {
    text: String
  },
  setup(props) {
    return () => (
      <>
        <Input value={props.text} />
      </>
    )
  }
})


const ReducedInput = (text: string) => (
  <>
    <Input value={text} />
  </>
)


export default defineComponent({
  setup() {
    return () => (
      <>
        <ComplexInput text={'text'} />
        {ReducedInput('text')}

        {/* <ReducedInput text="text"/> */}
      </>
    )
  }
})
